import hFormAlert from '@/components/h-form-alert/h-form-alert.vue'
export default {
	components: {
		hFormAlert
	},
	data() {
		return {
			content:"",
			status: 1,
			modal6: false,
		}
	},
	methods: {
		confirm(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了确定"
			})
		},
		cancel(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了取消"
			})
		},
		show6() {
			this.modal6 = true
		},
		hide6() {
			this.modal6 = false
		},
		close(e){
			uni.showToast({
				icon:"none",
				title:"点击提前结束"
			})
		}
	}
}
